import { useParams } from 'react-router-dom'
import { content_products } from '../map-content/MapContent'
import React, { useContext, useEffect, useState } from 'react'
import { useNavigate } from 'react-router-dom'
import Context from '../reducer/Context'
import { addItem, openCart, setData, itemAwait } from '../reducer/Actions'

const DetailItem = () => {

     const { name } = useParams()

     const item = content_products.find(item => {
          return item.name === name 
     })

     const consumer = useContext(Context)
     const { is_user } = consumer[0]
     const dispatch = consumer[1]
     const navigate = useNavigate()
     const [ quantity, setQuantity ] = useState(1)
     const [ addCart, setAddCart ] = useState(false)
     const [ buy, setBuy ] = useState(false)


     const handleOpenContext = target => {
          const context = document.querySelector('.context')
          const opacity_context = document.querySelector('.opacity-context')
          console.log(context)
          if(context.style.height !== "auto") {
               context.style.height = "auto"
               opacity_context.style.display = 'none'
               target.innerHTML = 'Thu Gn'
          } else {
               context.style.height = "24rem" 
               opacity_context.style.display = 'block'
               target.innerHTML = 'Xem thm'
          }
     }

     useEffect(() => {
          if(addCart) {
               item.amount = quantity
               if(is_user) {
                    dispatch(setData([]))
                    dispatch(addItem(item))
                    dispatch(openCart(true))
                    setQuantity(1)
               } else {
                    navigate('/dang-nhap')
                    dispatch(setData([]))
                    dispatch(itemAwait(item))
               }
               setAddCart(false)
          } 

     }, [addCart])

     useEffect(() => {
          if (buy) {
               item.amount = quantity
               if(is_user) {
                    dispatch(addItem(item))
                    setQuantity(1)
                    navigate('/thanh-toan')
               } else {
                    navigate('/dang-nhap')
                    dispatch(itemAwait(item))
               }
               setBuy(false)
          }
     }, [buy])

  return (
     <div className='width-screen mt-10 mb-20'>
          <div className='flex sm:flex-col mb:flex-col'>
               <div className='w-1/2 border-2 mr-5 mb:w-full sm:w-full'>
                    <img src={item.image} alt='' className='w-full' />
               </div>
               <div className='flex-1'>
                    <p className='text-3xl font-semibold mb:mt-3 mb:text-orange-500'>{item.name}</p>
                    <div className='flex my-3 mb:flex-col'>
                         <p className='text-lg'><span className='font-semibold'>Thng hiu:</span> Limited</p>
                         <p className='mx-2 mb:hidden'>l</p>
                         <p className='text-lg'><span className='font-semibold'>Tnh trng:</span> Cn hng</p>
                    </div>
                    <div className='flex items-center mb-5'>
                         <p className='text-3xl font-semibold text-green-500'>{item['new-price']}</p>
                         {
                              item.sell.isSell
                              &&
                              <React.Fragment>
                                   <p className='mx-5 line-through text-xl opacity-50'>{item.sell['old-price']}</p>
                                   <p className='px-2 bg-orange-600 rounded-sm text-white font-bold'>{item.sell['per-sell']}</p>
                              </React.Fragment>
                         }
                    </div>
                    <div className='flex items-center py-6 border-t-2 border-b-2'>
                         <p>S lng</p>
                         <div className='flex items-center border-2 ml-5 mb:ml-2 sm:ml-3'>
                              <p className='px-7 text-lg mb:px-7 sm:px-5'>{quantity}</p>
                              <div className='grid grid-rows-2 border-l-2 text-lg'>
                                   <button className='border-b-2 px-2'
                                        onClick={() => {
                                             setQuantity(prev => prev + 1)
                                             item.amount +=1
                                        }}
                                   >+</button>
                                   <button
                                        onClick={() => {
                                             if(item.amount < 2) {
                                                  item.amount = 1
                                             } else {
                                                  item.amount -= 1 
                                                  setQuantity(prev => prev - 1)
                                             }}
                                        }
                                   >-</button>
                              </div>
                         </div>
                    </div>
                    <p className='my-8'><i>M t ang cp nht</i></p>
                    <div className='grid grid-cols-2 tb-mb:grid-cols-1 gap-5'>
                         <div 
                              className='flex flex-col items-center px-5 py-2 bg-orange-500 hover:bg-green-600 cursor-pointer text-white rounded-xl lg:px-3'
                              onClick={() => setBuy(true)}              
                         >
                              <p className='text-xl font-semibold'>MUA NGAY</p>
                              <p className='text-sm'>Giao hng tn tay qu khch</p>
                         </div>
                         <div 
                              className='flex flex-col items-center px-5 py-2 bg-orange-100 hover:bg-green-600 cursor-pointer hover:text-white hover:border-green-600 text-orange-500 border-2 border-orange-500 rounded-xl lg:px-3'
                              onClick={() => setAddCart(true)}                                           
                         >     
                              <p className='text-xl font-semibold'>CHO VO GI</p>
                              <p className='text-sm'>Thm vo gi hng  chn tip</p>
                         </div>
                    </div>
               </div>
          </div>
          <div className='mt-10 flex flex-col'>
               <p className='max-w-max px-10 py-2 bg-green-600 text-white text-xl font-semibold rounded-t-lg'>M t sn phm</p>
               <div className='context relative h-96 pt-10 border-t-2 text-lg text-justify overflow-hidden'>
                    <span className='opacity-context'></span>
                    <p className='mb-5'>(M t mang tnh cht v d)</p>
                    <p className='mb-5'>Chui l mt loi thc phm, ng thi cng l mt dc liu thin nhin  h tr cho nhiu cn bnh. So vi qu to, chui c hm lng carbohydrate cao gp 2 ln, protein cao gp 4 ln, vitamin A v st cao gp 5 ln, nhng loi vitamin v khong cht khc cao gp 2 ln, hm lng phosphorus cao gp 3 ln.</p>
                    <p className='mb-5'>Nh li trn bo larry  queensland nm 2005  tn ph hu nh ton b cc vn chui trn tiu bang ny (queensland l th  chui ca c), khin gi c chui tng ln gp 10 ln. Lc ny, cc bnh nhn cao huyt  lc c r nhau n dch v chm sc y t  i tin mua chui, nhng v cha c ai lo xa n tnh hung ny nn dch v ny cng nh b tay. K cu chuyn ny ra, ch mun ni ln tm quan trng ca loi tri cy dn d ny.</p>
                    <p className='mb-5'>Chui l mt loi thc phm, ng thi cng l mt dc liu thin nhin  h tr cho nhiu cn bnh. So vi qu to, chui c hm lng carbohydrate cao gp 2 ln, protein cao gp 4 ln, vitamin A v st cao gp 5 ln, nhng loi vitamin v khong cht khc cao gp 2 ln, hm lng phosphorus cao gp 3 ln.</p>
                    <p className='mb-5'>Trong chui, hm lng kali (potassium) chim t l rt cao, cha nhiu loi ng thin nhin nh: fructose, sucrose, glucose, cung cp mt nng lng di do cho c th. Hai qu chui c th cung cp nng lng cho 90 pht luyn tp th thao. Khng nhng th, chui cn gip iu tr mt s bnh, nh , chui c xp vo hng top trong thc n hng ngy.</p>
                    <p className='mb-5'>Chui l ngun cung cp fructooligosaccharides, mt cht quan trng  nui dng nhng loi vi khun c li trong ng rut, gip chc nng rut hot ng tt hn, nh , c th s hp thu vitamin v cc cht dinh dng mt cch hiu qu hn. S tng hp thu ny s em nhiu canxi hn ti xng chng ta, gip cho b xng vng chc hn.</p>
                    <p className='mb-5'>i vi d dy, nhng hp cht c trong qu chui s gip nui dng t bo thnh rut, to nn mt hng ro dch nhy vng mnh c  sc  chin u chng li nhng loi vi khun gy l lot d dy.</p>
                    <p className='mb-5'>Khi chng ta b tiu chy (c bit trong ma dch tiu chy), chui c th cung cp tc thi cc cht in gii m c th b mt, trong , kali l cht quan trng nht. Kali l mt loi khong cht quan trng gip mang oxy ti no, iu ha nhp tim v cn bng nc trong c th.</p>
                    <p className='mb-5'>Nh hm lng cao kali v hm lng thp natri c trong chui  gip chui vn ln hng thc phm vua trong vic h huyt p v gim nguy c t qu. Theo tp ch The New England Journal of Medicine, n chui u n c th lm gim tn sut t vong do t qu ti 40%. Hin ti, FDA  cho php cc cng ty chui ng mc gim huyt p v ngn nga t qu trn cc bao b sn phm ca h.</p>
                    <p className='mb-5'>Khi b to bn, n chui s c cung cp cht x, vn lm gia tng chc nng rut. Khi mc cc chng bnh v d dy, chui cng gip ci thin triu chng do cha nhng cht c tc dng lm gim  acid (antacid).</p>
                    <p className='mb-5'>Trong chui cng cha mt hm lng cao st, kch thch c th to ra hemoglobin gip ngn chn s thiu mu. Mt hp cht khc c trong chui l tryptophan, cht ny vo c th s c chuyn ha thnh serotonin. y l mt cht ha hc gip iu ha trng thi c th. Cng vi serotonin, nhng vitamin nhm B trong chui cng gip iu ha glucose huyt (nng  glucose huyt s nh hng n trng thi tinh thn).</p>
                    <p className='mb-5'>Nhng loi ng thin nhin c trong chui gip iu ha nng  ng huyt, gip ci thin tnh trng stress do thc phm gy ra, gip ci thin sc khe cho thai ph. Chui thng c n dm sau nhng ba n s c tc dng kim sot nng  ng huyt.</p>
              </div>
              <button className='border-2 max-w-max px-8 py-2 self-center text-lg mt-5'
                    onClick={e => handleOpenContext(e.target)}
              >Xem thm</button>
          </div>
     </div>
  )
}

export default DetailItem